import config from '../config';

/**
 * GET request
 * @param {String} path the request path
 * @returns {Promise} the request
 */
const get = (path = '') => {
  return fetch(`${config.api}/${path}`).then((response) => response.json()).then((result) => Promise.resolve(result.data));
};

/**
 * POST request
 * @param {String} path the request path
 * @param {object} data the request body
 * @returns {Promise} the request
 */
const post = (path = '', data = {}) => {
  return fetch(`${config.api}/${path}`, {
    method: 'POST',
    cache: 'no-cache',
    headers: { 'Content-Type': 'application/json' },
    credentials: 'include',
    body: JSON.stringify(data)
  }).then((response) => response.json()).then((result) => Promise.resolve(result.data));
};

export default {
  get, post
};